.app-wrapper {
    width: 922px;
    background: #96949512;
    border-radius: 4px;
    overflow: hidden;
}

@media screen and (max-width: 992px) {
    .app-wrapper {
        width: 558px;
    }
}
@media screen and (max-width: 576px) {
    .app-wrapper {
        width: 286px;
    }
}